import styled from 'styled-components';

export default styled.label`
  width: calc(50% - 10px);
  font-family: "Merriweather Sans";
  font-size: 12px;
  font-weight: 400;
  color: #2c3e50;
  text-transform: uppercase;
  display:flex;
  flex-flow: column nowrap;
  @media only screen and (max-width: 768px) {
    width: calc(100% - 10px);
  }
`;



